<template>
  <div class="login">
    <van-nav-bar>
      <template #left>
        <div class="left-nav">
          <div class="logo">
            <img
              class="auto-img"
              src="../assets/images/logo.png"
              alt=""
            />
          </div>
          <div class="logo-title">Luckin Coffee</div>
        </div>
      </template>

      <template #right>
        <div class="right-title" @click="toMain">先逛一逛</div>
      </template>
    </van-nav-bar>

    <div class="login-box">
      <div class="login-info">
        <div>
          <div class="welcome">找回密码！</div>
          <div class="en-welcome">Find password</div>
        </div>

        <div class="login-form">
          <van-form @submit="changePassword()">
            <van-field
              v-model="loginUserInfo.phone"
              name="手机号"
              label="手机号"
              placeholder="手机号"
              autocomplete="off"
            />
            <van-field
              v-model="loginUserInfo.password"
              :type="loginPasswordStatus ? 'password' : 'text'"
              name="密码"
              label="密码"
              placeholder="密码必须为字母开头"
              :right-icon="loginPasswordStatus ? 'closed-eye' : 'eye-o'"
              autocomplete="off"
              @click-right-icon="togglePassword('loginPasswordStatus')"
            />

            <div class="forgot">
              <div class="forgot-text" @click="toLogin">已有账号,立即登录</div>
            </div>

            <div class="login-btn">
                <!-- @click="changePassword() -->
              <van-button round block color="#eb2403" native-type="submit" 
                >提交</van-button
              >
            </div>

            <div class="login-btn">
              <van-button round block @click="showPopup();toLogin()"
                >返回</van-button
              >
            </div>
          </van-form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      //是否显示弹出层
      show: false,

      //用户登录信息
      loginUserInfo: {
        phone: "",
        password: "",
      },

      //是否显示登录密码
      loginPasswordStatus: true,
    };
  },

  methods: {
    //显示弹出层
    showPopup() {
      this.show = true;
    },
    // changePassword(p1,p2){
    changePassword(){
        console.log(this.loginUserInfo.phone,this.loginUserInfo.password);
        this.axios({
        method: "POST",
        url: "/retrievePassword",
        // phone:p1,
        // password:p2
        phone:this.loginUserInfo.phone,
        password:this.loginUserInfo.password
      })
        .then((res) => {
          
          console.log("==============>",res);
          this.$toast(res.data.msg);

        //   if (res.data.code === 200) {
        //     //登录成功
        //     //将token保存在cookie
        //     this.$cookies.set('TOKEN', res.data.token, '10d');
        //     setTimeout(() => {
        //       this.$router.push({name: 'Menu'});
        //     }, 2000)
        //   }
          
        })
        .catch((err) => {
          
        });
    },
    // 跳转到主界面
    toMain(){
        this.$router.push({name: 'Menu'})
    },
    //切换注册密码状态
    togglePassword(key) {
      this[key] = !this[key];
    },

  toLogin(){
      this.$router.push({name: "Login"})
  },

    //登录
    login() {
      //表单验证
      //验证手机号
      let lgoinPhoneReg = /^1[3-9]\d{9}$/;
      if (!lgoinPhoneReg.test(this.loginUserInfo.phone)) {
        //手机号验证不通过
        this.$toast("手机号错误");

        return;
      }

      //验证密码
      //支持字母数字组合, 且必须以字母开头, 6-16位
      let lgoinPasswrodReg = /^[A-Za-z][A-Za-z\d]{5,15}$/;
      if (!lgoinPasswrodReg.test(this.loginUserInfo.password)) {
        this.$toast("密码支持字母数字组合");
        return;
      }


      

      //发起注册请求
      this.axios({
        method: "POST",
        url: "/login",
        data: this.loginUserInfo
      })
        .then((res) => {
          

          this.$toast(res.data.msg);

          if (res.data.code === 200) {
            //登录成功
            //将token保存在cookie
            this.$cookies.set('TOKEN', res.data.token, '10d');
            setTimeout(() => {
              this.$router.push({name: 'Menu'});
            }, 2000)
          }
          
        })
        .catch((err) => {
          
        });
    }
  },
};
</script>

<style lang="less" scoped>
.login {
  .reg-form {
    margin-top: 30px;
  }
  .register {
    padding: 15px 10px 10px;
  }

  .register-title {
    font-size: 18px;
  }

  .forgot {
    margin-top: 20px;
    display: flex;
  }

  .forgot-text {
    margin-left: auto;
    color: #666;
    font-size: 14px;
  }

  .login-btn {
    margin-top: 50px;
  }

  .login-form {
    margin-top: 50px;
  }

  .welcome {
    font-size: 24px;
    font-weight: bold;
  }

  .en-welcome {
    font-size: 18px;
    color: #999;
    margin-top: 20px;
  }

  .left-nav {
    display: flex;
    align-items: center;
  }

  .logo {
    width: 36px;
    height: 36px;
    border-radius: 50%;
  }

  .logo-title {
    margin-left: 10px;
    font-size: 14px;
    font-weight: bold;
  }

  .right-title {
    font-size: 14px;
    color: #eb2403;
    font-weight: bold;
  }

  .login-box {
    position: fixed;
    top: 46px;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #fff;
  }

  .login-info {
    padding: 100px 10px 10px;
  }
}
</style>